import React, {Component} from 'react'
import {
  Form as NativeForm,
  message,
  Modal,
  Button
} from 'antd'
import {
  API_POST_CONFIGURE_GROUPS_MANAGE_CREATED,
  API_PUT_CONFIGURE_GROUPS_MANAGE_EDIT
} from 'api'
import {getData} from 'util'
import AddPopupForm from './AddPopupForm'

class Form extends Component {
  constructor(props) {
    super(props)

    this.state = {
      loading: false // 是否显示loading
    }

    this.saveName = {} // 新增需保存名称
  }

  /* 存对应名称 */
  handleSaveName = (key, value) => {
    this.saveName[key] = value
  }

  /* 提交 */
  handleSubmit = () => {
    const {
      form: {validateFieldsAndScroll},
      handleAddCancel
    } = this.props
    validateFieldsAndScroll((err, values) => {
      if (err) return
      this.setState({loading: true})
      try {
        let params = {
          businessType: values.businessType,
          inventoryDcCode: values.inventoryDcCode && values.inventoryDcCode.split('\/')[0] || '',
          groupName: values.groupName || '',
          deliverySys: values.deliverySys || '',
          ...this.saveName
        }

        if (params.dcListConfig == '' || params.groupName == '' || params.deliverySys == '') return message.error('请输入必填内容')

        for (let idx in params) {
          if (params[idx] == '') delete params[idx]
        }

        // 判断新增还是修改
        let api = API_POST_CONFIGURE_GROUPS_MANAGE_CREATED
        if (this.props.record.id) { // 修改
          api = API_PUT_CONFIGURE_GROUPS_MANAGE_EDIT
          params.id = this.props.record.id
        }

        getData({api, data: params}).then(data => {
          this.setState({loading: false})
          if (data && data.message == 'success') {
            this.props.reloadTableData()
            handleAddCancel()
            message.success('提交成功')
          }
        }).catch(err => {
          this.setState({loading: false})
          message.error('提交失败')
          console.error(err)
        })
      } catch (err) {
        console.error(err)
      }
    })
  }

  render() {
    const {
      handleAddCancel
    } = this.props

    return (
      <Modal
        style={{top: '10px'}}
        visible={true}
        onOk={this.handleSubmit}
        onCancel={handleAddCancel}
        title='新增'
        footer={[
          <Button key="back" onClick={handleAddCancel}>取消</Button>,
          <Button key="submit" type="primary" loading={this.state.loading} onClick={this.handleSubmit}>
            确定
          </Button>
        ]}
      >
        <AddPopupForm
          handleSaveName={this.handleSaveName}
          saveName={this.saveName}
          {...this.state}
          {...this.props}
        />
    </Modal>)
  }
}

export default NativeForm.create()(Form)



