export default {
  // 商品类型
  goodsType: value => {
    switch (String(value)) {
      case '0':
        return '商品'
      case '1':
        return '小类'
      case '2':
        return '中类'
      case '3':
        return '大类'
    }
  },
  // 订单状态
  orderStatus: value => {
    switch (String(value)) {
      case 'WAIT_CUT':
        return '待接单'
      case 'WAIT_SEND':
        return '待发货'
      case 'STOCKOUT':
        return '配送中'
      case 'RECEIVED':
        return '已签收'
      case 'CANCELLED':
        return '已取消'
      case 'R_APPLY':
        return '差异待审'
      case 'R_PERMIT':
        return '处理中'
      case 'R_SUCCESS':
        return '处理完成'
      case 'R_REJECT':
        return '差异拒绝'
      case 'ALL':
        return '全部'
    }
  },
  // 发货系统
  orderSys: value => {
    switch (String(value)) {
      case 'SAP':
        return 'SAP'
      case 'WMS':
        return '红草'
      case 'MALL':
        return '中台'
    }
  },
  // 渠道
  merchantType: value => {
    switch (String(value)) {
      case 'HQBULK':
        return '红旗'
      case 'MANUAL_BULK':
        return '手工'
      case 'MAPP_BULK':
        return '小程序'
    }
  },
  //留言状态
  dealStatus: key => {
    switch (String(key)) {
      case '1':
        return '已处理'
      default:
        return '待处理'
    }
  },
  // 针对非(kg和g)单位的商品，所有数量不允许输入小数点
  productUnit: value => ['KG', 'G'].includes(String(value).toLocaleUpperCase()),
  // formatterTime: str => str ? str.toString().replace(/^(\d{4})(\d{2})(\d{2})$/, '$1-$2-$3') : ''
  // 客户返利查询 - 状态
  rebateType: key => {
    switch (String(key)) {
      case '1':
        return '退款'
      default:
        return '收款'
    }
  },
  // 客户返利查询 - 返利类型
  rebateStatus: key => {
    switch (String(key)) {
      case '1':
        return '已提交'
      case '2':
        return '作废'
      default:
        return '待提交'
    }
  },
  // 业务类型
  businessType: key => {
    switch (String(key)) {
      case '1001':
        return '大宗业务'
      case '1002':
        return 'B2B2B'
      case '1003':
        return '小B业务'
      default:
        return ''
    }
  }
}



