import React, {PureComponent} from 'react'
import {
  Table,
  Button
} from 'antd'
import moment from 'moment'
import style from '../style/Table.scss'
import {businessType} from 'yhUtil'

export default class TableComponent extends PureComponent {
  constructor() {
    super()

    this.holdValue = null // 暂存值

    this.state = {
      dataSources: [] // 原数据
    }

    this.columns = [
      // {
      //   title: '业务类型',
      //   dataIndex: 'businessType',
      //   key: 'businessType',
      //   width: 100,
      //   render: (text, record, index) => `${businessType(text)}`
      // },
      {
        title: '',
        key: '',
        dataIndex: '',
        width: 10
      }, {
        title: '分组名称',
        dataIndex: 'groupCode',
        key: 'groupCode',
        width: 100,
        render: (text, record, index) => `${text} ${record.groupName}`
      }, {
        title: '发货系统',
        dataIndex: 'deliverySys',
        key: 'deliverySys',
        width: 100,
        render: (text, record, index) => `${text === '0' ? '电子秤' : 'PDA'}`
      }, {
        title: '最后更新者',
        dataIndex: 'updatedBy',
        key: 'updatedBy',
        width: 100
      }, {
        title: '最新更新时间',
        dataIndex: 'updatedTime',
        key: 'updatedTime',
        width: 100,
        render: text => <span>{moment(text).format('YYYY-MM-DD HH:mm:ss')}</span>
      }, {
        title: '操作',
        dataIndex: 'handle',
        key: 'handle',
        width: 100,
        render: (text, record, index) => <Button onClick={this.handleEdit(record)}>修改</Button>
      }
    ]
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.searchResult !== nextProps.searchResult) {
      let list = [...nextProps.searchResult]
      list.power((item, index) => {
        item.key = item.id
      })

      this.setState({
        dataSources: list
      })
    }
  }

  /* 修改 操作 */
  handleEdit = record => e => this.props.handleShowEdit && this.props.handleShowEdit(record)

  render() {
    const {
      pageSizeChange,
      total,
      page,
      size,
      loading,
      handleSelectChange,
      updateSelectedRows,
      // selectedRows,
      selectedRowKeys
    } = this.props
    /* 选择功能的配置 */
    const rowSelection = {
      selectedRowKeys,
      onChange: handleSelectChange,
      onSelect: updateSelectedRows
    }

    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      onShowSizeChange: pageSizeChange, // pageSize 变化的回调
      className: `${style.pagination}`,
      onChange: pageSizeChange,
      pageSizeOptions: ['50', '100', '200', '500']
    }

    return (
      <div className={style['table-group']}>
        <Table
          className={style.table}
          loading={loading}
          // rowSelection={rowSelection}
          pagination={pagination}
          dataSource={this.state.dataSources}
          columns={this.columns}
          size="middle"
        />
      </div>
    )
  }
}



