import React, {PureComponent} from 'react'
import {
  Radio
} from 'antd'
import {
  CommonFormItem,
  CommonSelect,
  CommonInput
} from 'Form'
import {
   API_GET_CLASSIFY_ACCOUNT_BUSINESS_TYPE
} from 'api'
import {popupFormItemLayout} from 'enum'
import style from '../style/AddPopup.scss'

export default class AddPopupForm extends PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      businessTypelistConfig: {api: API_GET_CLASSIFY_ACCOUNT_BUSINESS_TYPE} // 业务类型
    }
  }

  componentDidMount() {
    const {
      record,
      form: {setFieldsValue}
    } = this.props

    if (record.id) {
      let initial = record.inventoryDcCode && record.inventoryDcName ? `${record.inventoryDcCode}/${record.inventoryDcName}` : undefined
      setFieldsValue({
        // businessType: record.businessType,
        inventoryDcCode: initial,
        groupName: record.groupName,
        deliverySys: record.deliverySys
      })
      this.handleSelect('inventoryDcCode', initial)
    }
  }

  /* 库存地点 内容变化 */
  handleChange = (type, value) => {
    const {
      saveName
    } = this.props

    // 去除重复选择
    let fieldsValue = saveName[type]
    if (!value || value == '' || typeof value !== 'string' || fieldsValue == value) return false

    this.handleSelect(type, value)
  }

  /* 库存地点 选择 */
  handleSelect = (type, value) => {
    const {
      handleSaveName,
      saveName
    } = this.props

    // 去除重复选择
    let fieldsValue = saveName[type]
    if (!value || value == '' || typeof value !== 'string' || fieldsValue == value) return false

    let name = ''
    try {
      name = value.split('\/').pop()
    } catch (err) {
      console.error(err)
    }

    handleSaveName('inventoryDcName', name)
  }

  render() {
    const {
      form: {getFieldDecorator},
      record
    } = this.props
    const {
      businessTypelistConfig
    } = this.state

    return (
      <div className={style.goodsNewPrieSettings_addCommodityPrices}>
        {/* <CommonSelect
          rules={[{required: true, message: '业务类型不可为空'}]}
          getFieldDecorator={getFieldDecorator}
          label="业务类型"
          showSearch
          // mode="multiple"
          decorator="businessType"
          listConfig={businessTypelistConfig}
          placeholder="请选择"
          listRenderGetValue={item => item.businessTypeCode}
          listRenderGetLabel={item => `${item.businessTypeCode} ${item.businessTypeName}`}
          // onChange={this.handleBusinessTypeChange}
          {...popupFormItemLayout}
        /> */}
        <CommonInput
          getFieldDecorator={getFieldDecorator}
          label="分组名称"
          decorator="groupName"
          placeholder="请输入"
          rules={[{required: true, message: '分组名称不可为空'}]}
          {...popupFormItemLayout}
        />
        <CommonFormItem
          getFieldDecorator={getFieldDecorator}
          label="发货系统"
          decorator="deliverySys"
          rules={[{required: true, message: '发货系统不可为空'}]}
          {...popupFormItemLayout}
          initialValue={record.deliverySys}
        >
          <Radio.Group>
            <Radio value={'0'}>电子秤</Radio>
            <Radio value={'1'}>PDA</Radio>
          </Radio.Group>
        </CommonFormItem>
      </div>
    )
  }
}



