import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col
} from 'antd'
import {
  CommonSelect,
  CommonInput
} from 'Form'
import {
  API_GET_CONFIGURE_CONFIGURE_DEVICE_SAP_CODE
} from 'api'
import {formItemLayout} from 'enum'
import style from '../style/Form.scss'

class App extends PureComponent {
  constructor() {
    super()

    this.state = {
      sapCusCodeListConfig: {api: API_GET_CONFIGURE_CONFIGURE_DEVICE_SAP_CODE} // 客户
    }
  }

  /* 客户名称 查询 */
  handleSapCodeSearch = searchKey => {
    if (searchKey) {
      let searchParams = {
        searchKey
      }
      this.setState({sapCusCodeListConfig: {api: API_GET_CONFIGURE_CONFIGURE_DEVICE_SAP_CODE, searchParams}})
    }
  }

  /* 搜索查询 */
  handleSearch = () => {
    const {
      form: {validateFieldsAndScroll},
      hacdleSearchResult
    } = this.props

    validateFieldsAndScroll((error, values) => {
      if (!error) {
        let params = {
          equipmentCode: values.equipmentCode || '',
          sapCusCode: values.sapCusCode || ''
        }
        hacdleSearchResult(params)
      }
    })
  }

  /* 搜索重置 */
  handleReset = () => this.props.form.resetFields()

  render() {
    const {
      sapCusCodeListConfig
    } = this.state
    const {
      form: {getFieldDecorator}
    } = this.props

    return (
      <Form className={style.form}>
        <Row type="flex" justify="space-between">
          <Col span={6}>
            <CommonInput
              getFieldDecorator={getFieldDecorator}
              label="设备ID"
              decorator="equipmentCode"
              placeholder="请输入设备ID"
              onPressEnter={this.handleSearch}
              {...formItemLayout}
            />
          </Col>
          <Col span={6}>
            <CommonSelect
              allowClear={false}
              required={true}
              getFieldDecorator={getFieldDecorator}
              label="客户"
              showSearch
              // mode="combobox"
              filterOption={false}
              decorator="sapCusCode"
              listConfig={sapCusCodeListConfig}
              onSearch={this.handleSapCodeSearch}
              placeholder="编码/名称"
              listRenderGetValue={item => item.sapCusCode}
              listRenderGetLabel={item => `${item.sapCusCode}/${item.sapCusName}`}
              {...formItemLayout}
            />
          </Col>
          <Col span={6}></Col>
          <Col span={6}>
            <Row type="flex" justify="end" className={style.mb15}>
              <Button type="primary" className={style.mr10} onClick={this.handleSearch} >查询</Button>
              <Button onClick={this.handleReset}>重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



