import React, {PureComponent} from 'react'
import {Bread} from 'Bread'
import {Table, Tool, AddPopup} from './components'
import {API_GET_CONFIGURE_GROUPS_MANAGE_SEARCH} from 'api'
import {getData} from 'util'

export default class App extends PureComponent {
  constructor() {
    super()

    this.state = {
      isShowAddPopUp: false, // 是否显示新增弹框
      searchResult: [], // 获取查询结果
      searchParams: {}, // 搜索传值
      total: 0, // 查询总数
      page: 1, // 当前页码
      size: 50, // 当前分页数
      tableLoading: false, // 列表loading
      record: {}, // 记录数据

      selectedRowKeys: [], // 选中的队列下标
      selectedRows: [] // 选中的队列数据
    }
  }

  componentWillMount() {
    this.hacdleSearchResult({})
  }

  /* 获取查询结果 */
  _fetchSearchResult = (params) => {
    if (!params) return false
    params = Object.assign({}, params, {
      page: this.state.page,
      size: this.state.size
    })
    this.setState({
      tableLoading: true
    })

    getData({
      api: API_GET_CONFIGURE_GROUPS_MANAGE_SEARCH,
      searchParams: params
    }).then((data) => {
      this.resetSelectedRowKeys()
      this.setState({
        tableLoading: false
      })
      if (data && data.result && data.result.result) {
        this.setState({
          total: data.result.totalNum,
          searchResult: data.result.result
        })
      }
    })
  }

  /* 搜索查询结果 */
  hacdleSearchResult = (params) => {
    if (!params) return false
    for (let idx in params) {
      if (params[idx] == '') delete params[idx]
    }
    this.setState(
      {
        page: 1,
        searchParams: params
      },
      () => this._fetchSearchResult(this.state.searchParams)
    )
  }

  /* 页码和每页显示数量改变 */
  pageSizeChange = (current, pageSize) => {
    if (!current || !pageSize) return false
    this.setState(
      {
        page: current,
        size: pageSize
      },
      () => this._fetchSearchResult(this.state.searchParams)
    )
  }

  /* 更新查询结果 - 刷新表格数据 */
  reloadTableData = () => this._fetchSearchResult(this.state.searchParams)

  /*---------------- 表格操作 -----------------*/
  /* 更新选中的数据 */
  handleSelectChange = (selectedRowKeys, selectedRows) =>
    this.setState({selectedRowKeys, selectedRows})

  /* 表格选中操作 */
  updateSelectedRows = (record, selected, selectedRows, nativeEvent) => {
    let arr = []
    selectedRows && selectedRows.map((item) => arr.push(item.key))
    this.handleSelectChange([...new Set(arr)], selectedRows)
  }

  /* 重置选中的key */
  resetSelectedRowKeys = () => {
    this.setState({
      selectedRowKeys: [],
      selectedRows: []
    })
  }
  /*---------------- 表格操作 -----------------*/

  /*---------------- 新增 弹框 -----------------*/
  /* 新增按钮 */
  handleAdd = () => {
    this.setState({
      record: {},
      isShowAddPopUp: true
    })
  }

  /* 新增关闭按钮 */
  handleAddCancel = () => {
    this.setState({
      record: {},
      isShowAddPopUp: false
    })
  }
  /*---------------- 新增 弹框 end-----------------*/

  /* 显示修改 弹框 */
  handleShowEdit = (record) => {
    this.setState({
      record,
      isShowAddPopUp: true
    })
  }

  render() {
    const {
      searchResult,
      isShowAddPopUp,
      total,
      page,
      size,
      tableLoading,
      selectedRows,
      selectedRowKeys,
      record
    } = this.state

    return (
      <section>
        <Bread route={['配置信息', '大宗配置', '分组管理']} />
        <Tool
          handleAdd={this.handleAdd}
          selectedRows={selectedRows}
          reloadTableData={this.reloadTableData}
          resetSelectedRowKeys={this.resetSelectedRowKeys}
        />
        <Table
          loading={tableLoading}
          total={total}
          page={page}
          size={size}
          searchResult={searchResult}
          selectedRowKeys={selectedRowKeys}
          pageSizeChange={this.pageSizeChange}
          handleAdd={this.handleAdd}
          reloadTableData={this.reloadTableData}
          handleSelectChange={this.handleSelectChange}
          updateSelectedRows={this.updateSelectedRows}
          handleShowEdit={this.handleShowEdit}
        />
        {isShowAddPopUp && (
          <AddPopup
            record={record}
            handleAddCancel={this.handleAddCancel}
            reloadTableData={this.reloadTableData}
          />
        )}
      </section>
    )
  }
}



